<template>
  <div>
    <h1>POSTS</h1>
    <input type="text" v-model="text" />
    <button @click="postPost()" class="btn">GO</button>
    <div
      @contextmenu="handler($event)"
      class="post-container"
      v-for="post in posts"
      :key="post._id"
    >
      <div id="context-menu">
        <div @click="delet_post(post._id)" class="item">
          <i class="fas fa-trash-alt"></i> Delete
        </div>
      </div>
      <p class="post-date">{{ post.CreatedAt }}</p>
      <p class="post-text">{{ post.text }}</p>
    </div>
  </div>
</template>

<script>
// window.addEventListener("contextmenu",function(event){
//   event.preventDefault();
//   var contextElement = document.getElementById("context-menu");
//   contextElement.style.top = event.offsetY + "px";
//   contextElement.style.left = event.offsetX + "px";
//   contextElement.classList.add("active");
// });

import axios from "axios";
// import PostServices from '../services'
export default {
  name: "posts",

  data() {
    return {
      posts: [],
      text: "",
    };
  },
  methods: {
    handler: function(event) {
      event.preventDefault();
      var clientY = event.clientY;
      var clientX = event.clientX;

      var contextElement = document.getElementById("context-menu");
      contextElement.style.top = clientY + "px";
      contextElement.style.left = clientX + "px";
      contextElement.classList.add("active");
      var posts = document.querySelector(".post-container");
      posts.addEventListener("click", function() {
        document.getElementById("context-menu").classList.remove("active");
      });
    },
    postPost() {
      axios
        .post("api", {
          text: this.text,
        })
        .then((this.text = ""), this.get_all());
      this.get_all();
    },
    get_all() {
      axios
        .get("api")
        .then((post) => (this.posts = post.data.reverse()));
    },
    delet_post(id) {
      axios
        .delete("api/" + id)
        .then((this.text = ""), this.get_all());
      this.get_all();
    },
  },

  created() {
    this.get_all();
  },
};
</script>

