  border: 1px solid #35495e;
  font-weight: bold;
  font-family: monospace;
}
.post-text {
  font-size: 30px;
}
.post-date {
  position: absolute;
  left: 0;
  top: 0;
  padding: 0;
  margin: 2px 2px;
  font-size: 15px;
}
.del-btn {
  position: absolute;
  right: 0;
  top: 0;
}
#context-menu {
  position: fixed;
  z-index: 10000;
  width: 150px;
  background: #1b1a1a;
  border-radius: 5px;
  transform: scale(0);
  transform-origin: top left;
}
#context-menu.active {
  transform: scale(1);
  transition: transform 100ms ease-in-out;
}
#context-menu .item {
  padding: 8px 10px;
  font-size: 15px;
  color: #eee;
}
#context-menu .item:hover {
  background: #555;
  cursor: pointer;
}
#context-menu .item i {
  display: inline-block;
